import * as React from 'react';
import { useEffect, useRef, useState } from 'react';

import L from 'leaflet';
import 'leaflet/dist/leaflet.css';
import 'leaflet.markercluster';
import 'leaflet.markercluster/dist/MarkerCluster.css';
import icon from 'leaflet/dist/images/marker-icon.png';
import iconShadow from 'leaflet/dist/images/marker-shadow.png';

import {Grid, Paper, Divider, Typography, Slider, Box, OutlinedInput, InputLabel, MenuItem, FormControl, FormGroup, FormControlLabel, ListItemText, Select, Checkbox, Switch, ButtonBase, Button, Tooltip, Table, TableBody, TableCell, TableHead, TableRow, TableContainer, Accordion, AccordionSummary, AccordionDetails} from '@mui/material';
import {CancelOutlined, Task, ScheduleSend, ExpandMore, Send, FileOpen, NoteAdd} from '@mui/icons-material';

import data from '../assets/data.json';
import csvMairies from '../assets/mairies.csv';

/* Descripteurs BDD SITADEL */
const etatDAU = {'2': 'Autorisé', '4' : 'Annulé', '5' : 'Commencé', '6' : 'Terminé'}
const typeServicePublic = {'1' : 'Transport', '2' : 'Enseignement et recherche', '3' : 'Santé', '4' : 'Action sociale', '5' : 'Ouvrage spécial', '6' : 'Culture et loisir', '7' : 'Non rempli', '8' : 'Plusieurs types renseignés'};
const natureProjet = {'1': "Nouvelle construction", '2': "Travaux sur construction existante"};
const label = {'HAB' : 'Habitable', 'LOC' : 'Total Locaux', 'HEB' : 'Hébergement', 'BUR' : 'Bureaux', 'COM' : 'Commerce', 'ART' : 'Artisanat', 'IND' : 'Industrie', 'AGR' : 'Agricole/Forestier', 'ENT' : 'Entrepôt', 'PUB' : 'Service public'};
const details = {'HAB' : 'Surface habitable', 'LOC' : 'Locaux autres que habitation', 'HEB' : 'Hébergement hôtelier', 'BUR' : 'Bureaux', 'COM' : 'Commerce', 'ART' : 'Artisanat', 'IND' : 'Industrie', 'AGR' : 'Exploitation agricole ou forestière', 'ENT' : 'Entrepôt', 'PUB' : 'Service public ou d\'intérêt collectif'};

function renduLigneTypeSurface(type, dataRow){
        /* Créée la ligne du tableau en fonction du type de surface, HAB/LOC/HEB/BUR/COM/ART/IND/AGR/ENT/PUB */

        if (label.hasOwnProperty(type)) {
            if(dataRow[`SURF_${type}_AVANT`]+dataRow[`SURF_${type}_CREEE`]+dataRow[`SURF_${type}_ISSUE_TRANSFO`]+dataRow[`SURF_${type}_DEMOLIE`]+dataRow[`SURF_${type}_TRANSFORMEE`] == 0) {
                return (<></>); // On n'affiche pas les lignes ou toutes les valeurs sont nulles.
            }
            let fw = (type == 'LOC' ? 700 : 500);
            const customTableCell = (value) => {return value == 0 ? <TableCell sx={{fontWeight: 300}} align="left">{value}</TableCell> : <TableCell sx={{fontWeight: fw}} align="left">{value}</TableCell>};

            return (
                <TableRow key={type} sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
                    <Tooltip followCursor={true} title={details[type]}><TableCell sx={{fontWeight: fw}} component="th" scope="row">{label[type]}</TableCell></Tooltip>
                    {customTableCell(dataRow[`SURF_${type}_AVANT`])}
                    {customTableCell(dataRow[`SURF_${type}_CREEE`])}
                    {customTableCell(dataRow[`SURF_${type}_ISSUE_TRANSFO`])}
                    {customTableCell(dataRow[`SURF_${type}_DEMOLIE`])}
                    {customTableCell(dataRow[`SURF_${type}_TRANSFORMEE`])}
                </TableRow>
            );
        }
        else {
            return (<></>);
        }
}

function affichageSITADEL(dataRow){

    return (<>
        <Grid container spacing={2} sx={{height : '100%'}}>
            <Grid item xs={9}>
                <TableContainer component={Box} sx={{tableLayout: 'fixed'}}>
                    <Table size='small' sx={{maxWidth: '100%'}}>
                <TableHead>
                    <TableRow sx={{ "& th": { border: 0 } }}><TableCell>Adresse terrain</TableCell><TableCell>{`${dataRow['ADR_NUM_TER']} ${dataRow['ADR_TYPEVOIE_TER']} ${dataRow['ADR_LIBVOIE_TER']} ${dataRow['ADR_LIEUDIT_TER']} ${dataRow['ADR_CODPOST_TER']} ${dataRow['ADR_LOCALITE_TER']}`}</TableCell></TableRow>
                </TableHead>
                <TableBody>
                    <TableRow sx={{ "& td": { border: 0 } }}><TableCell>Catégorie de DAU</TableCell><TableCell align='left'>{dataRow['Type_DAU'] === 'PC' ? 'PC' : 'DP'}</TableCell></TableRow>
                    <TableRow sx={{ "& td": { border: 0 } }}><TableCell>Nature projet</TableCell><TableCell align='left'>{natureProjet[dataRow['NATURE_PROJET']]}</TableCell></TableRow>
                    <TableRow sx={{ "& td": { border: 0 } }}><TableCell>Etat DAU</TableCell><TableCell align='left'>{dataRow['Etat_DAU']}</TableCell></TableRow>
                    <TableRow sx={{ "& td": { border: 0 } }}><TableCell>Date demande</TableCell><TableCell align='left'>{dataRow['DATE_REELLE_AUTORISATION']}</TableCell></TableRow>
                    {(dataRow['DATE_REELLE_DOC']) ? <TableRow sx={{ "& td": { border: 0 } }}><TableCell>Date ouverture du chantier</TableCell><TableCell align='left'>{dataRow['DATE_REELLE_DOC']}</TableCell></TableRow> : <></>}
                    {(dataRow['DATE_REELLE_DAACT']) ? <TableRow sx={{ "& td": { border: 0 } }}><TableCell>Date achèvement des travaux</TableCell><TableCell align='left'>{dataRow['DATE_REELLE_DAACT']}</TableCell></TableRow> : <></>}
                    <TableRow sx={{ "& td": { border: 0 } }}><TableCell>Demandeur</TableCell><TableCell align='left'>{(dataRow['DENOM_DEM'] == " ") ? 'Non renseigné' : dataRow['DENOM_DEM']}</TableCell></TableRow>
                    {(dataRow['TYPE_SERVICE_PUBLIC'] != 7) ? <TableRow sx={{ "& td": { border: 0 } }}><TableCell>Type service public</TableCell><TableCell align='left'>{typeServicePublic[dataRow['TYPE_SERVICE_PUBLIC']]}</TableCell></TableRow> : <></>}
                </TableBody>
            </Table>
                </TableContainer>
            </Grid>
            <Grid item xs={3}>
                <Box sx={{display: 'flex', flexDirection: 'column', alignItems: 'left'}}>
                    <Button size='small' variant='contained' color= 'secondary' disableElevation sx={{width: '100%', mb: '6px'}} endIcon={<Send/>}>Contacter Mairie</Button>
                    <Button size='small' variant='contained' color='primary' disableElevation sx={{width: '100%', mb: '6px'}} endIcon={<NoteAdd/>}>Ajouter Permis</Button>
                    <Button size='small' disabled variant='contained' color='primary' disableElevation sx={{width: '100%', mb: '6px'}} endIcon={<FileOpen/>}>Consulter Permis</Button>
                    <Box sx={{display: 'flex', flexAlign:'row', pl: '10px', pr: '10px', alignItems: 'center'}}>
                        <Typography variant='body2'>Site intéressant</Typography>
                        <Switch size='small' defaultChecked color="warning" sx={{ml:'auto'}}/>
                    </Box>

                </Box>
            </Grid>
        </Grid>
        <TableContainer component={Box} sx={{tableLayout: 'fixed'}}>
            <Table size='small'>
                <TableHead sx={{backgroundColor: 'rgba(0, 0, 0, .03)'}}>
                  <TableRow>
                      <TableCell>Surface</TableCell>
                      <Tooltip followCursor={true}title="Surface existante avant travaux"><TableCell align="left">Existante</TableCell></Tooltip>
                      <Tooltip followCursor={true}title="Surface nouvelle construite"><TableCell align="left">Construite</TableCell></Tooltip>
                      <Tooltip followCursor={true}title="Surface créée par changement de destination"><TableCell align="left">Transformée</TableCell></Tooltip>
                      <Tooltip followCursor={true}title="Surface supprimée (démolie)"><TableCell align="left">Démolie</TableCell></Tooltip>
                      <Tooltip followCursor={true}title="Surface supprimée par changement de destination"><TableCell align="left">Supprimée</TableCell></Tooltip>
                  </TableRow>
                </TableHead>
                <TableBody>
                    {renduLigneTypeSurface('HEB', dataRow)}
                    {renduLigneTypeSurface('BUR', dataRow)}
                    {renduLigneTypeSurface('COM', dataRow)}
                    {renduLigneTypeSurface('ART', dataRow)}
                    {renduLigneTypeSurface('IND', dataRow)}
                    {renduLigneTypeSurface('AGR', dataRow)}
                    {renduLigneTypeSurface('ENT', dataRow)}
                    {renduLigneTypeSurface('PUB', dataRow)}
                    {renduLigneTypeSurface('LOC', dataRow)}
                    {renduLigneTypeSurface('HAB', dataRow)}
                </TableBody>
            </Table>
        </TableContainer>
    </>);
}

function MapFComponent(props) {

    /* Propriétés d'affichage */
    const ITEM_HEIGHT = 48;
    const ITEM_PADDING_TOP = 8;
    const MenuProps = {PaperProps: {style: {maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP, width: 250,},},};
    const secteurs = ['Habitable', 'Hébergement', 'Bureaux', 'Commerce','Artisanat', 'Industrie','Agricole/Forestier', 'Entrepôt', 'Service public'];

    /* Initialisation du state*/
    // Filtres
    const [annee, setAnnee] = useState(2016);
    const [surface, setSurface] = useState(5000);
    const [nouveauxSites, setNouveauxSites] = useState(true);
    const [dejaVisites, setDejaVisites] = useState(false);
    const [listeSecteurs, setListeSecteurs] = useState([]);
    // Site consulté
    const [detailsOuverts, setDetailsOuverts] = useState(false); // Affichage ou non des filtres
    const [precZoom, setPrecZoom] = useState(12);   // Historique du zoom avant clic sur site
    const [cleSelectionnee, setCleSelectionnee] = useState("01306321G0031"); // DAU du site sélectionné
    const [zoneUrba, setZoneUrba] = useState('chargement...'); // Variable locale pour stocker le retour de l'API Geoportail
    const [empriseSite, setEmpriseSite] = useState([]); //idem
    const [infoGeoPortail, setInfoGeoPortail] = useState(null);

    // Bindings leaflet
    const [mapInstance, setMapInstance] = useState(null);
    const mapRef = useRef(null);
    const empriseRef = useRef(null);

    const firstUpdate = useRef(true);

    /* Appel API zone-urba */
    const getZoneUrba = () => {
        if (firstUpdate.current) {
            firstUpdate.current = false;
            return;
        } // Pour empêcher un appel API au premier rendu
        console.log(`Début GetZoneUrba ${cleSelectionnee}`)
        let payload = `{"type":"Point","coordinates":[${data[cleSelectionnee]['lon']},${data[cleSelectionnee]['lat']}]}`;
        var urlZoneUrba = new URL("https://apicarto.ign.fr/api/gpu/zone-urba");
        urlZoneUrba.searchParams.append('geom', payload);

        fetch(urlZoneUrba).then(
            (result) => {
                if (result.status != 200) { throw new Error("Bad Server Response"); }
                return result.json();
            }).then((response) => {
                console.log('Result.json()');
                console.log(response);
                //console.log(`TotalFeatures ${response.totalFeatures}`);

                setInfoGeoPortail(response);

                switch (response.totalFeatures) {
                case 0:
                    setZoneUrba('Aucune zone trouvée !');
                    break;
                    case 1:
                    //console.log(`${response.features[0].properties.libelong}`);
                    setZoneUrba(<>
                            Zone : {response.features[0].properties.typezone}<br/>
                            Description : {response.features[0].properties.libelong} ({response.features[0].properties.libelle})
                        </>);
                    setEmpriseSite(response.features[0].geometry.coordinates[0][0]);
                    break;
                default:
                    setZoneUrba('Trop de résultats !');
                    break;
            }
        }).catch((error) => {
            console.log(error);
            setZoneUrba('Erreur dans l\'appel GeoPortail');
            setEmpriseSite([]);});

        /*setZoneUrba('API Call désactivé');
        setEmpriseSite([]);*/

    };

    /* Clic sur marqueur Leaflet */
    const onMarkerClick = (e) => {
        console.log('Marker click !')
        let key = e.sourceTarget.options.title;

        setZoneUrba('Chargement...');
        setDetailsOuverts(true);

        if (mapRef.current) {
            setPrecZoom(mapRef.current.getZoom());
            mapRef.current.invalidateSize();
            mapRef.current.flyTo(e.target.getLatLng(), 14);
        }

        setCleSelectionnee(key);
    };

    /* Gestion de l'appel async de zone-urba*/
    useEffect(() => {
        getZoneUrba();
    }, [cleSelectionnee]);

    /* Affichage de l'emprise de la zone d'urbanisme */
    useEffect(() => {
        if (!mapInstance) return;
        // Nettoyage d'un eventuel polygone déjà affiché
        if (empriseRef.current) {
            empriseRef.current.removeFrom(mapInstance);
        }
        // Affichage d'un nouveau polygone
        empriseRef.current = L.polygon(empriseSite.map(e => [e[1], e[0]])).addTo(mapInstance); // latitude et longitude sont inversées pour leaflet par rapport aux retours des API GeoPortail.

    }, [empriseSite]);

    /* [BOUCHON] Application des filtres - à remplacer par un appel à une BDD SQL pour récupérer un nouveau json data contenant toutes les informations SITADEL */
    const applyFilters = () => {
        const msg = `Filtres :\nAnnée ${annee}\nSurface ${surface}\nNouveaux sites ${nouveauxSites}\nDéjà visités ${dejaVisites}\nType ${listeSecteurs}`;
        alert(msg);
    };

    /* Fermeture des détails */
    const fermerDetails = () => {
        if (empriseRef.current) empriseRef.current.removeFrom(mapInstance);
        setDetailsOuverts(false);
        setTimeout(() => {
            mapRef.current.invalidateSize()
            mapRef.current.setZoom(precZoom);
        }, 0);
    };

    /* Sélecteur de secteur */
    const handleChange = (event) => {
        const {
            target: {value},
        } = event;
        setListeSecteurs(typeof value === 'string' ? value.split(',') : value)
    };

    /* Initialisation de la carte // Runs only on the first render */
    useEffect(() => {
        console.log(csvMairies);


        if (mapInstance) return;
        let myIcon = L.icon({
            iconUrl: icon,
            shadowUrl: iconShadow,
            iconSize: [25, 41],
            iconAnchor: [13, 41],
            popupAnchor: [0, 0],
            shadowSize: [41, 41],
            shadowAnchor: [13, 41]
        });

        //const position0 = [43.5626632, 5.0249413];
        //const key = "01306321G0031";

        mapRef.current = L.map('map', {
            center: [48.8481232, 2.351374], // Position initiale (France)
            zoom: 6, // 6 pour avoir toute le pays
            scrollWheelZoom: true,
            tap: false
        });
        /*var tiles = L.tileLayer('https://api.mapbox.com/styles/v1/{id}/tiles/{z}/{x}/{y}?access_token=pk.eyJ1IjoibWFwYm94IiwiYSI6ImNpejY4NXVycTA2emYycXBndHRqcmZ3N3gifQ.rJcFIG214AriISLbB6B5aw', {
            maxZoom: 18,
            attribution: 'Imagery &copy; <a href="https://www.mapbox.com/">Mapbox</a>',
            id: 'mapbox/streets-v11',
            tileSize: 512,
            zoomOffset: -1
        }).addTo(map);*/ // MapBox Tiles
        L.tileLayer('https://{s}.tile.openstreetmap.fr/osmfr/{z}/{x}/{y}.png', {
            maxZoom: 18,
            attribution: '&copy; <a href="https://openstreetmap.org/copyright">OpenStreetMap</a> contributors'
        }).addTo(mapRef.current);

        //L.marker(position, {icon: myIcon, title: key}).on('click', onMarkerClick).addTo(map);

        var markersCluster = new L.MarkerClusterGroup();

        for (let key in data) {
            var position = new L.LatLng(data[key]['lat'], data[key]['lon']);
            var marker = new L.Marker(position, {icon: myIcon, title: key});
            marker.on('click', onMarkerClick);
            markersCluster.addLayer(marker);
        }

        mapRef.current.addLayer(markersCluster);

        //mapRef.current.on('click', (e) => {console.log(e.latlng); mapRef.current.invalidateSize();});

        setMapInstance(mapRef.current);
    }, []);

    /* Rendu du composant React */
    return (
        <Grid container spacing={2} sx={{height : '100%'}}>
            <Grid item xs={detailsOuverts ? 4 : 8} sx={{height : '100%', pb: '16px'}}>
                <Paper sx={{height: '100%'}} id='map'>
                </Paper>
            </Grid>
            {/* Filtres */}
            <Grid item xs={detailsOuverts ? 8 : 4} sx={{height: '100%', pb: '16px'}}>
                <Box sx={{position: 'relative', flex: 1, height: '100%', width: '100%'}}>
                    {detailsOuverts ?
                        <Paper sx={{position: 'absolute', height: '100%', width: '100%', overflowX: 'hidden', overflowY: 'auto'}}>
                            {/* Header détails site sélectionné */}
                            <Box sx={{position: 'sticky', zIndex: 100, top: '0', backdropFilter: 'blur(10px)', boxShadow: "inset 0px -1px 1px #e7ebf0", backgroundColor: 'rgba(158,158,158,1.0)', padding : '6px 8px 6px 8px', color: '#ffffff', display: 'flex', flexDirection: 'row', alignItems: 'center'}}>
                                <ButtonBase  sx={{color: 'inherit'}} onClick={fermerDetails}>
                                    <Tooltip title="Fermer détails">
                                        <CancelOutlined/>
                                    </Tooltip>
                                </ButtonBase>
                                <Typography variant='body1' sx={{marginLeft : '6px', fontWeight : '500',}}>
                                    Numéro DAU : {cleSelectionnee}
                                </Typography>
                                <Box sx={{marginLeft: 'auto'}}>
                                    <Tooltip title="PDF du permis reçu">
                                        <Task/>
                                    </Tooltip>
                                    <Tooltip title="Mail envoyé en mairie">
                                        <ScheduleSend/>
                                    </Tooltip>
                                </Box>
                            </Box>
                            {/* Contenu détails site sélectionné */}
                            <Box sx={{padding : '8px'}}>
                                {affichageSITADEL(data[cleSelectionnee])}
                                <br/>
                                <Accordion disableGutters elevation={1} square sx={{'&:not(:last-child)': {border: 0,}, '&:before': {display: 'none',},}} defaultExpanded={true}>
                                    <AccordionSummary expandIcon={<ExpandMore />} elevation={1} sx={{backgroundColor : 'rgba(0, 0, 0, .03)'}}>
                                      <Typography sx={{fontWeight: 300, fontSize: '0.875rem'}}>Informations GEOPORTAIL</Typography>
                                    </AccordionSummary>
                                    <AccordionDetails sx={{borderTop: '1px solid', borderColor: '#e0e0e0'}}>
                                        <Typography sx={{fontWeight: 300, fontSize: '0.750rem'}}>
                                            {zoneUrba}
                                        </Typography>
                                    </AccordionDetails>
                                </Accordion>
                                <Accordion disableGutters elevation={1} square sx={{'&:not(:last-child)': {border: 0,}, '&:before': {display: 'none',},}}>
                                    <AccordionSummary expandIcon={<ExpandMore />} elevation={1} sx={{backgroundColor : 'rgba(0, 0, 0, .03)'}}>
                                      <Typography sx={{fontWeight: 300, fontSize: '0.875rem'}}>Informations complètes SITADEL</Typography>
                                    </AccordionSummary>
                                    <AccordionDetails sx={{borderTop: '1px solid', borderColor: '#e0e0e0'}}>
                                        <Box sx={{fontWeight: 300, fontSize: '0.750rem'}}>
                                            {Object.keys(data[cleSelectionnee]).map((keyName, i) => (
                                                <div key={keyName}>
                                                    {keyName} : {data[cleSelectionnee][keyName]} <br/>
                                                </div>
                                            ))}
                                        </Box>
                                    </AccordionDetails>
                                </Accordion>
                                <Accordion disableGutters elevation={1} square sx={{'&:not(:last-child)': {border: 0,}, '&:before': {display: 'none',},}} defaultExpanded={true}>
                                    <AccordionSummary expandIcon={<ExpandMore />} elevation={1} sx={{backgroundColor : 'rgba(0, 0, 0, .03)'}}>
                                      <Typography sx={{fontWeight: 300, fontSize: '0.875rem'}}>Debug</Typography>
                                    </AccordionSummary>
                                    <AccordionDetails sx={{borderTop: '1px solid', borderColor: '#e0e0e0'}}>
                                        <Typography sx={{fontWeight: 300, fontSize: '0.750rem'}}>
                                            Latitude OSM : {data[cleSelectionnee]['lat']}<br/>
                                            Longitude OSM : {data[cleSelectionnee]['lon']}<br/>
                                        </Typography>
                                    </AccordionDetails>
                                </Accordion>
                            </Box>
                        </Paper>
                        :
                        <Paper sx={{position: 'absolute', height: '100%', width: '100%', overflowX: 'hidden', overflowY: 'auto'}}>
                            {/* Header filtres */}
                            <Box sx={{position: 'sticky', zIndex: 100, top: '0', backdropFilter: 'blur(10px)', boxShadow: "inset 0px -1px 1px #e7ebf0", backgroundColor: 'rgba(21,101,192,0.7)', textAlign: 'center'}}>
                                <Button variant='text' sx={{width: '100%', color: '#ffffff'}} onClick={() => {applyFilters();}}>
                                    Appliquer filtres
                                </Button>
                            </Box>
                            {/* Contenu filtres */}
                            <Box sx={{padding : '8px'}}>
                                <Typography>Année</Typography>
                                <Slider aria-label="Temperature" value={annee} getAriaValueText={(value) => '$value'} valueLabelDisplay="on" marks min={2013} max={2021} step={1} onChange={(e, value) => {setAnnee(value)}}/>

                                <Typography>Surface minimale</Typography>
                                <Slider aria-label="slider2" value={surface} getAriaValueText={(value) => `${value}`} valueLabelDisplay="on" min={0} max={10000} color="secondary" onChange={(e, value) => {setSurface(value)}}/>

                                <FormGroup>
                                    <FormControlLabel control={<Switch checked={nouveauxSites} color='warning'
                                                                       onChange={(e, value) => {
                                                                           setNouveauxSites(value)
                                                                       }}/>}
                                                      label="Nouveaux sites uniquement"/>
                                    <FormControlLabel control={<Switch checked={dejaVisites}
                                                                       onChange={(e, value) => {
                                                                           setDejaVisites(value)
                                                                       }}/>}
                                                      label="Sites déjà visités"/>
                                </FormGroup>

                                <FormControl sx={{m: 1, width: 'clamp(50px, 300px, calc(100% - 24px))'}}>
                                    <InputLabel id="demo-multiple-checkbox-label">Secteur</InputLabel>
                                    <Select
                                        labelId="demo-multiple-checkbox-label"
                                        id="demo-multiple-checkbox"
                                        multiple
                                        value={listeSecteurs}
                                        onChange={handleChange}
                                        input={<OutlinedInput label="Secteur"/>}
                                        renderValue={(selected) => selected.join(', ')}
                                        MenuProps={MenuProps}
                                    >
                                        {secteurs.map((sec) => (
                                            <MenuItem key={sec} value={sec}>
                                                <Checkbox checked={listeSecteurs.indexOf(sec) > -1}/>
                                                <ListItemText primary={sec}/>
                                            </MenuItem>
                                        ))}
                                    </Select>
                                </FormControl>
                            </Box>
                        </Paper>
                    }

                </Box>
            </Grid>
        </Grid>);
}

export default MapFComponent;